.response {
  &__container {
    @include content-padding;
  }

  &__content {
    @include page-grid;
  }

  &__wrapper {
    @include page-grid;

    grid-column: 1/-1;

    @include on-desktop {
      grid-column: 3/11;
    }
  }

  &__scobs {
    position: relative;
    top: 50%;

    width: 100%;
    height: auto;

    grid-column: 2/4;

    @include on-tablet {
      top: 30%;
      grid-column: 4/6;
    }

    @include on-desktop {
      grid-column: 5/9;
    }
  }

  &__text {
    font-size: 14px;
    font-weight: 700;
    line-height: 20px;
    text-align: center;
    color: $color-text-primary;

    grid-column: 1/-1;
  }

  &__author {
    display: flex;
    flex-direction: column;
    align-items: center;

    grid-column: 2/4;

    @include on-tablet {
      grid-column: 4/6;
    }

    @include on-desktop {
      grid-column: 6/8;
    }
  }

  &__author-img {
    width: 62px;
    height: 62px;

    margin-top: 30px;

    @include on-tablet {
      margin-top: 40px;
    }

    @include on-desktop {
      margin-top: 30px;
    }
  }

  &__author-name {
    font-size: 16px;
    font-weight: 700;
    line-height: 22px;
    text-align: center;
    color: $color-text-primary;

    margin-top: 10px;

    @include on-tablet {
      margin-top: 20px;
    }
  }

  &__author-job {
    font-size: 14px;
    font-weight: 400;
    line-height: 21px;
    text-align: center;
    color: $color-text-secondary;

    margin-top: 5px;
  }
}
