.question {
  &__container {
    @include content-padding;
  }

  &__content {
    @include page-grid;
  }

  &__title {
    font-size: 30px;
    font-weight: 700;
    line-height: 40px;
    text-align: left;
    text-transform: uppercase;
    color: $color-text-primary;

    grid-column: 1/-1;

    @include on-tablet {
      font-size: 36px;
      line-height: 46px;

      grid-column: 1/5;
    }

    @include on-desktop {
      font-size: 56px;
      line-height: 70px;

      grid-column: 1/7;
    }
  }

  &__form {
    grid-column: 1/-1;

    display: flex;
    flex-direction: column;

    @include on-tablet {
      grid-column: 5/-1;
    }

    @include on-desktop {
      grid-column: 7/-1;
    }
  }

  &__form-input {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;

    margin-top: 27px;

    height: 48px;

    outline: none;

    background-color: $color-background;
    border: 1px solid $color-border-primary;

    @include on-tablet {
      margin-top: 0;
    }

    padding: 12px 20px;

    &:focus {
      border: 1px solid #0c797a;
    }
  }

  &__form-textarea {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;

    margin-top: 20px;

    padding: 12px 20px;
    resize: none;

    outline: none;

    height: 128px;

    background-color: $color-background;
    border: 1px solid $color-border-primary;

    &:focus {
      border: 1px solid #0c797a;
    }
  }

  &__form-button {
    font-size: 12px;
    font-weight: 700;
    line-height: 14px;
    text-align: center;
    text-transform: uppercase;

    cursor: pointer;

    color: #fff;
    background-color: $color-button-primary;

    border: none;

    width: 110px;
    padding-block: 13px;
    margin-top: 30px;

    &:hover {
      background-color: #0c797a;
    }
  }
}
