.header {
  &__middle {
    @include page-grid;

    margin-top: 50px;

    overflow: hidden;
  }

  &__middle-img {
    width: 100%;
    height: auto;

    transform: translate(15%);

    grid-column: 1/-1;

    @include on-tablet {
      align-self: center;

      z-index: -1;

      transform: translate(50px);

      grid-row: 1/3;
      grid-column: 4/-1;
    }

    @include on-desktop {
      grid-row: 1/4;
      grid-column: 5/-1;
    }
  }

  &__middle-title {
    font-size: 40px;
    font-weight: 700;
    line-height: 52px;
    text-transform: uppercase;

    color: $color-text-primary;

    margin-top: 40px;

    grid-column: 1/-1;

    @include on-tablet {
      font-size: 48px;
      line-height: 62px;

      grid-column: 1/4;
    }

    @include on-desktop {
      font-size: 62px;
      line-height: 78px;

      margin-top: 81px;

      grid-column: 1/5;
    }
  }

  &__middle-description {
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    text-align: left;
    color: $color-text-primary;

    margin-top: 20px;

    grid-column: 1/-1;

    @include on-tablet {
      grid-column: 1/4;
    }

    @include on-desktop {
      margin-top: 30px;

      grid-column: 1/5;
    }
  }
}
